.s-loader {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

@media only screen and (max-width: 1024px) {
  .s-loader {
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
}

@media only screen and (max-width: 512px) {
  .s-loader {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
  }
}
@media only screen and (max-width: 300px) {
  .s-loader {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 80vh;
  }
}
